.movie {
  margin: 2.5%;
  background-color: white;
  padding: 10px;
  height: 200px;
  width: 45%;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  max-width: 30%;
  max-height: 180px;
  float: left;
}

.movie .textArea {
  max-width: 60%;
  margin-left: 5%;
  float: left;
  max-height: 180px;
}

.movie .movie_summary {
  font-size: 14px;
  color: #4d4d4d;
  clear: left;
}

.movie .genres_genre {
  list-style: none;
  font-style: italic;
  color: grey;
  float: left;
  font-size: 12px;
  margin-right: 10px;
  padding-top: 0px;
  margin-top: 0px;
  padding-bottom: 4px;
  margin-bottom: 0px;
}

.movie ul {
  padding-top: 0px;
  margin-top: 0px;
  padding-left: 0px;
}

.movie .movie_year {
  font-weight: 300;
  color: gray;
  padding-top: 4px;
  margin-top: 0px;
  padding-bottom: 4px;
  margin-bottom: 0px;
}

.movie .movie_title {
  padding-top: 0px;
  margin-top: 0px;
  padding-bottom: 0px;
  margin-bottom: 0px;
}
